import forumobjct from "@/components/script/forumobjct";
import merge from "webpack-merge";
import videoobject from "@/components/script/videoobject";
import video_index_tab from "@/components/video/index/tab";
import '@/assets/css/sspai.css'

export default {
  name: "c_video_list",
  mixins: [forumobjct, videoobject],
  components: {video_index_tab},
  data() {
    return {
      page: 1,
      page_num: undefined,
      fid: undefined,
      forum_conf: undefined,
      forum_topic_data: undefined,
      forum_top_topic_data: undefined,
      video_class_name: ''
    }
  },
  created() {
    window._t = this;
    this.load_page();
  }, methods: {
    load_page: function () {
      this.load_list();
      this.load_top_list();
      this.forum_conf = this.get_forum(this.fid);
      console.log('create video_list');
      this.video_class_name = this.video_col_class_name();
      console.log('video_class_name:', this.video_class_name);
      this.set_title(this.forum_conf.name);
    },
    on_page_change: function () {
      console.log('[on_page_change]page:', this.page);
      this.$router.push({
        query: merge(this.$route.query, {'page': this.page})
      });
    },
    on_load_top_topic: function (response) {
      this.forum_top_topic_data = response.data;
      console.log('on_load_top_topic:', response);
      console.log('forum_top_topic_data:', this.forum_top_topic_data);
    },
    on_load_topic: function (response) {
      console.log('on_load_topic:', response);
      this.forum_topic_data = response.data;

      this.page_num = parseInt((this.forum_topic_data.total_count / this._g.PER_PAGE_COUNT));
      console.log('page_num:', this.page_num);
    },
    load_list: function () {
      var urlvars = this.getUrlVars();
      if (urlvars.page) {
        this.page = parseInt(urlvars.page);
      }
      console.log('this.$route.params.id:', this.$route.params.id);
      this.fid = this.$route.params.id;
      var start = (this.page - 1) * this._g.PER_PAGE_COUNT;
      this.get_topic_list({
        fid: this.fid,
        start: start,
        limit: this._g.PER_PAGE_COUNT,
        settotop: false,
        from_cache: this.from_cache(),
        online_videos: true,
        cb: this.on_load_topic
      });
    },
    load_top_list: function () {
      var urlvars = this.getUrlVars();
      var from_cache = !this.$web_conf.debug;
      if (urlvars.from_cache) {
        from_cache = JSON.parse(urlvars.from_cache);
      }
      this.get_topic_list({
        fid: this.fid,
        start: 0,
        limit: this._g.PER_PAGE_COUNT,
        settotop: true,
        from_cache: from_cache,
        online_videos: true,
        cb: this.on_load_top_topic
      });
    }
  },
  watch: {
    '$route'(to, from) {
      this.forum_topic_data = undefined;
      this.load_page();
    }
  }
}
